import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { Text, Localizer } from 'preact-i18n';

import Images from '../../components/form/images';
import MiniAdGraph from '../../components/admin/miniAdGraph';

import mu from '../../lib/musher-util';

const initialState = {
	showAddComment: false,
};

@observer
class Input extends Component {
    handleImageErrored = (e) => {
        const image = e.target;
        image.onerror = null;
        setTimeout(() => {
            image.src += `?${new Date()}`;
        }, 1000);
    }
    
    removeImage = async (e) => {
        const { adStore } = this.props.stores;
        const { ad } = this.props;
        const imageName = e.target.parentElement.dataset.name || e.target.dataset.name;
        adStore.removeImage({
            id: ad.id,
            name: imageName,
        });
    }

    removeMainImage = () => {
        const { ad, field, updateField } = this.props;
        updateField({ id: ad.id, field, value: {} });
    }

    render() {
        // const { saved, dog, type = 'text', datalist = [], min, max, step, field, title, icon, help, onInput, updateField } = this.props;
        const { saved, ad, type = 'text', rows, field, title, icon, help, onInput, updateField } = this.props;
        const value = ad[field];
        if (type === 'image') {
            return (
                <div class={`form-group ${saved[`${field}.${ad.id}`] ? 'is-valid' : ''}`}>
                    <label for={`${field}Input`}>{icon && <i class={`${icon} text-muted`} />} {title}</label>

                    <div class='container-fluid'>
                        <div class='row'>
                            {ad.images && ad.images.map(img => (
                                <div class='col-3 p-1'>
                                    <img src={img.s3SmallLink} class='img-fluid' onError={this.handleImageErrored} />
                                    <button class='btn btn-sm btn-link'
                                        onClick={this.removeImage}
                                        data-name={img.name}
                                    ><i class='fas fa-trash' /> Del</button>
                                </div>
                            ))}
                        </div>
                    </div>
                    <Images saved={saved} object={ad} field={field} updateField={updateField} />
                    {saved[`${field}.${ad.id}`] ? <div class={`valid-feedback`} style='display: block;'><Text id='ad.changes-saved'>Endringene er lagret</Text></div> : 
                        <small id={`${field}Help`} class='form-text text-muted'>{help}</small>}
                </div>
            );
        }
        if (type === 'file') {
            return (
                <div class={`form-group ${saved[`${field}.${ad.id}`] ? 'is-valid' : ''}`}>
                    <label for={`${field}Input`}>{icon && <i class={`${icon} text-muted`} />} {title}</label>
                    <Files saved={saved} object={ad} field={field} updateField={updateField} />
                    {saved[`${field}.${ad.id}`] ? <div class={`valid-feedback`} style='display: block;'><Text id='ad.changes-saved'>Endringene er lagret</Text></div> : 
                        <small id={`${field}Help`} class='form-text text-muted'>{help}</small>}
                </div>
            );
        }
        if (type === 'textarea') {
            return (
                <div class={`form-group ${saved[`${field}.${ad.id}`] ? 'is-valid' : ''}`}>
                    <label for={`${field}Input`}>{icon && <i class={`${icon} text-muted`} />} {title}</label>
                    <textarea type={type} class={`form-control ${saved[`${field}.${ad.id}`] ? 'is-valid' : ''}`} id={`${field}Input`} aria-describedby={`${field}Help`}
                        value={value}
                        rows={rows}
                        data-id={ad.id}
                        data-field={field}
                        onInput={onInput}
                    />
                    {saved[`${field}.${ad.id}`] ? <div class={`valid-feedback`} style='display: block;'><Text id='ad.changes-saved'>Endringene er lagret</Text></div> : 
                        <small id={`${field}Help`} class='form-text text-muted'>{help}</small>}
                </div>
            );
        }

        return (
            <div class={`form-group ${saved[`${field}.${ad.id}`] ? 'is-valid' : ''}`}>
                <label for={`${field}Input`}>{icon && <i class={`${icon} text-muted`} />} {title}</label>
                <input type={type} class={`form-control ${saved[`${field}.${ad.id}`] ? 'is-valid' : ''}`} id={`${field}Input`} aria-describedby={`${field}Help`}
                    value={value}
                    data-id={ad.id}
                    data-field={field}
                    onInput={onInput}
                />
                {saved[`${field}.${ad.id}`] ? <div class={`valid-feedback`} style='display: block;'><Text id='ad.changes-saved'>Endringene er lagret</Text></div> : 
                    <small id={`${field}Help`} class='form-text text-muted'>{help}</small>}
            </div>
        );
    }
}


@observer
class InputNew extends Component {
    onInput = (e) => {
        const { field } = this.props;
        const value = e.target.value;
        const { adStore } = this.props.stores;
        // console.log('adStore.newAd', adStore.newAd);
        adStore.updateObjectKeyValue('newAd', field, value);
    }

    render() {
        const { field, icon, title, help } = this.props;
        const { adStore } = this.props.stores;
        const { newAd } = adStore;
        return (
            <div class='form-group'>
                <label for={`${field}Input`}>{icon && <i class={`${icon} text-muted`} />} {title}</label>
                <input type='text' class={`form-control`} id={`${field}Input`} aria-describedby={`${field}Help`}
                    onInput={this.onInput}
                    value={newAd[field]}
                />
                <small id={`${field}Help`} class='form-text text-muted'>{help}</small>
            </div>
        );
    }
}

@observer
class Ad extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
        this.updateTimer = {};
		this.inputRef = {};
    }

	async loadAd() {
		this.setState({ isLoading: true });
		const { adStore } = this.props.stores;
		await adStore.load({ query: { showall: 1 }, addData: ['adViews', 'adClicks'] });
		this.setState({ isLoading: false });
	}

	resetAd() {
        const { adStore } = this.props.stores;
        adStore.updateKeyValue('newAd', {});
	}

    insertAd = async () => {
		const { adStore } = this.props.stores;
		const { newAd } = adStore;
		await adStore.insert({ ...newAd });
        await adStore.loadAd({ query: { showall: 1 } });
        this.resetAd();
	}

    selectAd = async (e) => {
        const adId = e.target.parentElement.dataset.id || e.target.dataset.id;
        const { adStore } = this.props.stores;
        const adContent = await adStore.loadById({ id: adId, showall: 1 });
        console.log({ adContent });
        adStore.updateKeyValue('ad', adContent);
    }

    updateField = (e) => {
        const { id, field, type, elementtype, isNew } = e.target.dataset;
        const value = e.target.value;
        this.updateFieldValue({ id: parseInt(id, 10), field, value, type, elementtype, isNew });
    }

    updateFieldValue = ({ id, field, value, datatype, elementtype, isNew = false, skipTimer = false }) => {
        const { adStore } = this.props.stores;
        let newValue = value;

        let localObjectValue = value;
        const { ad } = adStore;
        if (field === 'images') {
            if (Array.isArray(ad.images) && Array.isArray(value)) {
                localObjectValue = [...ad.images, ...value];
            }
        }
    
        if (datatype === 'array' && !Array.isArray(value)) {
            if (elementtype === 'number') {
                newValue = value.split(',').map(e => parseFloat(e) || e);
            } else  {
                newValue = value.split(',');
            }
            // console.log('updateFieldValue', id, field, value, datatype, elementtype);
        }

        if (isNew) {
            adStore.updateObjectKeyValue('newAd', field, value);
        } else {
            adStore.updateField(id, field, localObjectValue);
            if (skipTimer) {
                this.doUpdateField(id, field, newValue);
            } else {
                clearTimeout(this.updateTimer[field]);
                this.updateTimer[field] = setTimeout(async () => {
                    this.doUpdateField(id, field, newValue);
                }, 500);
            }
        }
    }

    async doUpdateField(id, field, value) {
        const { adStore } = this.props.stores;
        await adStore.saveField(id, field, value);
    }

	componentDidMount() {
		this.loadAd();
	}

    render() {
        const { adStore } = this.props.stores;
        const { ad = {}, ads = [], saved, adViews, adClicks } = adStore;

        let graphFieldsLeft = [];
        let graphLegendsLeft = [];
        let graphFieldsRight = [];
        let graphLegendsRight = [];
        if (ads && ads.length > 0) {
            ads.forEach((ad) => {
                graphFieldsLeft.push(`adViews.${ad.id}`);
                graphLegendsLeft.push(`${ad.id}: ${ad.title} (${ad.page})`);
                graphFieldsRight.push(`adClicks.${ad.id}`);
                graphLegendsRight.push(`${ad.id}: ${ad.title} (${ad.page})`);
            });
        }

        return (
            <>
                <div class='w-100 mb-3'>
                    <div class='row'>
                        <div class='col-12 mb-0 position-relative'>
                            {ads && ads.length > 0 && <>
                                <table class='table table-sm table-striped'>
                                    <thead>
                                        <tr>
                                            <th>id</th>
                                            <th>active</th>
                                            <th>title</th>
                                            <th>page</th>
                                            <th>url</th>
                                            <th>email</th>
                                            <th>views</th>
                                            <th>clicks</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {ads.map(ad => (
                                            <tr data-id={ad.id} onClick={this.selectAd}>
                                                <td>{ad.id}</td>
                                                <td>{ad.active}</td>
                                                <td>{ad.title}</td>
                                                <td>{ad.page}</td>
                                                <td>{ad.url}</td>
                                                <td>{ad.email}</td>
                                                <td>{ad.viewCounts}</td>
                                                <td>{ad.clickCounts}</td>
                                            </tr>
                                        ))}
                                    </tbody>
                                </table>
                            </>}
                        </div>
                    </div>
    
                    <MiniAdGraph stores={this.props.stores}
                        fieldsLeft={graphFieldsLeft}
                        legendLeft={graphLegendsLeft}
                        fieldsRight={graphFieldsRight}
                        legendRight={graphLegendsRight}
                    />

                    <div class='row'>
                        {(!ad || !ad.id) && <div class='col-12 col-sm-8 col-lg-6 offset-sm-2 offset-lg-3 mt-5 mb-0 position-relative'>
                            <h5>Ny annonse</h5>
                            <InputNew stores={this.props.stores} field='title' icon='fas fa-heading'
                                title={<Text id='ad.title'>Tittel</Text>}
                                help={<Text id='ad.title-help'>Tittel på denne annonsen.</Text>} />
                            <button type='button' class='btn btn-primary'
                                onClick={this.insertAd}
                            >
                                <Text id='ad.save'>Registrer ny annonse</Text>
                            </button>
                        </div>}
                    </div>

                    {ad && ad.id && <div class='row'>
                        <div class='col-12 col-sm-8 col-lg-6 offset-sm-2 offset-lg-3 mt-5 mb-0 position-relative'>
                            <h5>Endre annonse: "{ad.title}"</h5>

                            <Input saved={saved} ad={ad} field='title' icon='fas fa-heading'
                                title={<Text id='ad.title'>Title</Text>}
                                help={<Text id='ad.title-help'>Title of this ad story</Text>}
                                onInput={this.updateField} />

                            <Input saved={saved} ad={ad} field='page' icon='fas fa-heading'
                                title={<Text id='ad.page'>Page ('/', '/workouts/', '/workouts/detail', '/dogs', '/dogs/detail')</Text>}
                                help={<Text id='ad.page-help'>Page to show this ad</Text>}
                                onInput={this.updateField} />
                            
                            <Input saved={saved} ad={ad} field='url' icon='fas fa-heading'
                                title={<Text id='ad.url'>URL</Text>}
                                help={<Text id='ad.url-help'>Ad url</Text>}
                                onInput={this.updateField} />

                            <Input saved={saved} ad={ad} field='target' icon='fas fa-heading'
                                title={<Text id='ad.target'>Target</Text>}
                                help={<Text id='ad.target-help'>Ad url target</Text>}
                                onInput={this.updateField} />

                            <Input saved={saved} ad={ad} field='email' icon='fas fa-heading'
                                title={<Text id='ad.email'>Email</Text>}
                                help={<Text id='ad.email-help'>Ad email</Text>}
                                onInput={this.updateField} />


                            <Input saved={saved} ad={ad} field='offline' icon='fas fa-heading'
                                title={<Text id='ad.offline'>Offline</Text>}
                                help={<Text id='ad.offline-help'>Ad offline</Text>}
                                onInput={this.updateField} />

                            <Input saved={saved} ad={ad} field='active' icon='fas fa-heading'
                                title={<Text id='ad.active'>Active</Text>}
                                help={<Text id='ad.active-help'>Ad active</Text>}
                                onInput={this.updateField} />

                            <Input saved={saved} ad={ad} field='startDate' icon='fas fa-heading'
                                title={<Text id='ad.startDate'>Start Date</Text>}
                                type='date'
                                help={<Text id='ad.startDate-help'>Ad startDate</Text>}
                                onInput={this.updateField} />

                            <Input saved={saved} ad={ad} field='endDate' icon='fas fa-heading'
                                title={<Text id='ad.endDate'>End Date</Text>}
                                type='date'
                                help={<Text id='ad.endDate-help'>Ad endDate</Text>}
                                onInput={this.updateField} />


                            <Input saved={saved} ad={ad} field='body' icon='fas fa-quote-right'
                                type='textarea' rows={5}
                                title={<Text id='ad.body'>Body</Text>}
                                help={<Text id='ad.body-help'>Body of this ad story</Text>}
                                onInput={this.updateField} />

                            <Input saved={saved} ad={ad} field='image' type='image' icon='fas fa-camera'
                                stores={this.props.stores}
                                title={<Text id='ad.photo'>Bilder</Text>} 
                                help={<Text id='ad.photo-help'>Images for this ad story.</Text>}
                                onInput={this.updateField} updateField={this.updateFieldValue} />
                        </div>
                    </div>}
                </div>
            </>
        );
    }
}

export default Ad;
